import React from 'react';

const LinuxNetwork = (props) => {

  return (
    <div className="common_style">

      <div className='block1'>
        <details>
          <summary> CentOS IP,  IP   </summary>
          <div className='block2'>

            <h2 className='h2'>   </h2>
            <li> vi /etc/sysconfig/network-scripts/ifcfg-ens33 <small> #       </small>
              <div className="block4">
                <li> BOOTPROTO=static <small> # [static=IP, dhcp=, bootp=?, none] </small> </li>
                <li> PREFIX=24 <small> # #24   ,  ? </small> </li>
                <li> IPADDR=[IP] <small> # (ex 192.168.10.20),  IP </small>  </li>
                <li> DNS1=[DNS] <small> # (ex 8.8.8.8, 168.126.63.1) </small> (ex 168.126.63.1) </li>
                <li> GATEWAY=[] <small> # (ex 192.168.10.2),   </small> </li> <br />

                <li> ONBOOT=yes <small> #     </small> </li>
                <li> BROADCAST=@.@.@.255 <small> #   </small> </li>
                <li> DEVICE=ens33 <small> #     . </small> </li>
                <li> HWADDR=@:@:@:@:@:@ <small> MAC </small> </li>
                <li> NETMASK <small> #   </small> </li>
                <li> NETWORK <small> #   </small> </li>
                <li> PEERDNS=yes <small> # dhcp dns resolv.conf   </small> </li>
                <li> DNS1 </li>
                <li> DNS2 </li>
                <li> USERCTL=no <small> #      </small> </li>
              </div>
            </li>
          </div>
        </details>
      </div>

      <div className='block1'>
        <details>
          <summary>   </summary>
          <div className='block2'>
            <h2 className='h2'>   </h2>
            <li> systemctl restart network <small> #   </small>  </li>
            <li> ping [IP] <small> # IP ping    
                , 168.126.63.1(KT) </small>  </li>
            <li> ifconfig <small> #     </small> </li>
            <li> ip route <small> #   ip  </small>  </li>
            <li> ip addr <small> #  </small>  </li>
            <li> vi /etc/resolv.conf <small> # nameserver  </small> </li>
            <li> netstat -lntp <small> #  </small>  </li>
            <li> netstat -antp <small> #  </small>  </li>
            <li> netstat -antp | grep LISTEN <small> #  </small>  </li>
          </div>
        </details>
      </div>

      <div className='block1'>
        <details>
          <summary>   </summary>
          <div className='block2'>

            <h2 className='h2'>    </h2>
            <div className="block4">
              <li> firewall-cmd --reload <small> #  (   ) </small> </li>
              <li> firewall-cmd --state <small> #   </small> </li>
              <li> firewall-cmd --list-ports <small> #    </small> </li>
              <li> firewall-cmd --list-all <small> #    </small> </li>
              <li> firewall-cmd --get-zones  <small> # </small> </li>
              <li> firewall-cmd --list-all-zones <small> # </small> </li>
              <li>  <small> # </small> </li>
              <li> systemctl start firewalld <small> #   </small> </li>
              <li> systemctl enable firewalld <small> #    </small> </li>
              <li> systemctl stop firewalld <small> #   </small> </li>
              <li> systemctl disable firewall <small> #   x </small> </li>
            </div>

            <h2 className='h2'>    </h2>
            <div className="block4">
              <li> firewall-cmd --permanent --add-port=[]/[(ex) tcp] <small> #    </small> </li>
              <li> firewall-cmd --permanent --remove-port=[ -]/[(ex) tcp)] </li>
              <li> firewall-cmd --permanent --add-service=[(ex) http https] </li>
              <li> firewall-cmd --permanent --remove-service=[] </li>
              <li> firewall-cmd --permanent --add-source=[IP]  <small> #  IP  </small> </li>
              <li> firewall-cmd --permanent --remove-source=[IP]  <small> #  IP  </small> </li>
              <li> firewall-cmd --permanent --add-source=[IP/]  <small> #  IP  </small> </li>
              <li> firewall-cmd --permanent --remove-source=[IP/]  <small> #  IP  </small> </li>
              <li> firewall-cmd --permanent --add-rich-rule='rule family="ipv4" source address=[IP] reject' <small> # IP   </small> </li>
              <li> firewall-cmd --permanent --add-rich-rule='rule family="ipv4" source address=[IP] drop' <small> # IP   </small> </li>
              <li> firewall-cmd --permanent --remove-rich-rule='rule family="ipv4" source address=[IP] reject' <small> # IP    </small> </li>
              <li> firewall-cmd --permanent --remove-rich-rule='rule family="ipv4" source address=[IP] drop' <small> # IP    </small> </li>
              <li> firewall-cmd --permanent --add-rich-rule='rule family="ipv4" source address=[IP] port port=[] protocol="tcp" accept' <small> # IP Port </small> </li>
              <li> firewall-cmd --permanent --remove-rich-rule='rule family="ipv4" source address=[IP] port port=[] protocol="tcp" accept' <small> # IP Port  </small> </li>
              <li> firewall-cmd --permanent --add-rich-rule='rule family="ipv4" source address=[IP/] port port=[] protocol="tcp" accept' <small> # IP Port  </small> </li>
              <li> firewall-cmd --permanent --remove-rich-rule='rule family="ipv4" source address=[IP/] port port=[] protocol="tcp" accept' <small> # IP Port   </small> </li>
            </div>

            <h2 className='h2'>     </h2>
            <div className="block4">
              <li className="col_red">  : FirewallD is not running </li>
              <li className="col_blue">  : systemctl start firewalld </li>
            </div>

            <h2 className='h2'>    ? </h2>
            <div className="block4">
              <li> vi /usr/lib/systemd/system/firewalld.service </li>
            </div>


          </div>
        </details>
      </div>

      <div className='block1'>
        <details>
          <summary> SELinux </summary>
          <div className='block2'>

            <h2 className='h2'>   </h2>
            <li> sestatus <small> # selinux   </small>   </li>
            <li> vi /etc/selinux/config
              <li> SELINUX=[disabled, enforcing] <small> #  </small> </li>
            </li>
            <li> setenforce [0,1] <small> # 0 , 1 ,    </small> </li>
            <li>  </li>

          </div>
        </details>
      </div>

      <div className='block1'>
        <details>
          <summary> httpd </summary>
          <div className='block2'>

            <h2 className='h2'>    </h2>
            <li> rpm -qa httpd <small> # httpd   </small> </li>
            <li> yum install -y httpd <small> # httpd  </small> </li>
            <li> systemctl status httpd, systemctl status httpd.service <small> # httpd    </small> </li>
            <li> apachectl configtest <small> # Syntax Error  </small> </li>
            <li>  </li>

          </div>
        </details>
      </div>

      <div className='block1'>
        <details>
          <summary> iptables  - <small> #   </small> </summary>
          <div className='block2'>

            <h2 className='h2'>   </h2>
            <li>        </li>
            <li> INPUT, FORWARD, OUTPUT :  ,  ,   </li>
            <li> SNAT DNAT : SNAT IP IP , DNAT IP IP  </li>
            <li> NEW(), ESTABLISHED(), INVALID(), RELATED() </li>
            <li> DROP, ACCEPT <small> #      </small> </li>

            <h2 className='h2'>   </h2>
            <li> rpm -qa | grep iptables <small> # iptables   </small> </li>
            <li> systemctl enable iptables </li>
            <li> systemctl start iptables </li>
            <li> systemctl status iptables </li>
            <li>  </li>
            <li> iptables -F <small> #   </small> </li>
            <li> iptables -N <small> #   </small> </li>
            <li> iptables -L <small> #   ,   </small> </li>
            <li> iptables -X <small> #    </small> </li>
            <li> iptables -P <small> #    </small> </li>
            <li> iptables -A [INPUT]<small> #      </small> </li>
            <li> iptables -I <small> #       </small> </li>
            <li> iptables -R <small> #    </small> </li>
            <li> iptables -D <small> #   </small> </li>
            <li> iptables -C <small> #   </small> </li>
            <li> iptables -s [IP] <small> #   </small> </li>
            <li> iptables -d [IP,] <small> #   </small> </li>
            <li> iptables -p [TCP, ] <small> #  </small> </li>
            <li> iptables -i <small> #     </small> </li>
            <li> iptables -o <small> #     </small> </li>
            <li> iptables -f <small> #  ? </small> </li>
            <li> iptables -j [DROP, ACCEPT]<small> #       </small> </li>
            <li> recent --update --seconds [] --hitcount [] --name HTTP
              <small> #     (    ) </small> </li>
            <li> --sport [] <small> #   </small> </li>
            <li> --dport [, :(??)] <small> #   </small> </li>
            <li> --tcp-flags [SYN,RST,ACK] [SYN,RST,ACK] <small> # TCP    </small> </li>
            <li> -m state --state [NEW, ESTABLISHED, INVALID RELATED] <small> #      </small> </li>
            <li>  </li> <br />

            <li> vi /etc/sysconfig/iptables
              <li> -A PREROUTING -i eth0 -p tcp -m tcp --dport [] -j DNAT --to-destination [IP] </li>
              <li>  </li>
            </li>
            <li> /etc/init.d/iptables restart </li>

            <h2 className="h2">   </h2>
            <li> iptables -t nat -A PREROUTING -p tcp --dport [1] -j REDIRECT --to-port [2] <small> # 1 2  </small> </li>
            <li> iptables -t nat -D PREROUTING -p tcp --dport [1] -j REDIRECT --to-port [2] <small> # 1 2     </small> </li>
            <li> iptables -I INPUT -p tcp -s 0.0.0.0/0 -d [IP] --dport [] -j ACCEPT <small> #   IP:     </small> </li>
            <li> iptables -I FORWARD -m tcp -p tcp -d [IP] --dport [] -j ACCEPT <small> #  IP:   </small> </li>
            <li> iptables -I FORWARD -m state -p tcp -d [IP/] --state NEW,RELATED,ESTABLISHED -j ACCEPT <small> # NEW,RELATED,ESTABLISHED      </small> </li>
            <li> iptables -t nat -I PREROUTING -p tcp -d [IP1] --dport [1] -j DNAT --to-destination [IP2:2] <small> # IP1:1   IP2:2  </small>   </li>
            <li> iptables -t nat -nL <small> #    </small> </li>
            <li> iptables -l INPUT 1 -p tcp --dport [] -j ACCEPT <small> # </small>  </li>
            <li> iptables --list </li>
            <li>  </li>

          </div>
        </details>
      </div>

    </div>
  );
}

export default LinuxNetwork;