import React from 'react';

const SpringCors = (props) => {

    return (
        <div className="common_style">
            <div className='block1'>
                <details>

                    <summary> Cors   </summary>
                    <div className='block2'>

                        <h2 className='h2'>    </h2>
                        <li>   </li>
                        <li> allow all origins </li>
                        <li> allow "simple" methods GET,HEAD and POST </li>
                        <li> allow all headers </li>
                        <li> set max age to 1800 seconds </li>

                        <h2 className='h2'>      </h2>
                        <div className="block4">
                            <li>     </li>
                            <li> @CrossOrigin("*") <small> #   </small> </li>
                            <li> @CrossOrigin(origins="*", allowedHeaders="*") <small> #    </small> </li>
                            <li>  </li>
                            <li>  </li>
                        </div>

                        <h2 className='h2'>    () </h2>
                        <div className="block4">
                            <li> @Configuration </li>
                            <li> public class WebConfig implements WebMvcConfigurer {'{'}
                                <li> @Override </li>
                                <li> public void addCorsMappings(CorsRegistry registry) {'{'}
                                    <li> registry.addMapping("/**").allowCredentials(true)
                                        <li> .allowedOrigins("http://127.0.0.1:3000"); <small> #  localhost  </small></li>
                                    </li>
                                </li>
                                <li> {'}'} </li>
                            </li>
                            <li> {'}'} </li>
                        </div>
                        <h2 className='h2'>     </h2>
                        <div className="block4">
                            <li> @Configuration </li>
                            <li> public class WebConfig implements WebMvcConfigurer {'{'}
                                <li> private final long MAX_AGE_SECS = 3600; </li>
                                <li> @Override </li>
                                <li> public void addCorsMappings(CorsRegistry registry) {'{'}
                                    <li> registry.addMapping("/**").allowCredentials(true)
                                        <li> .allowedOrigins("*") //  </li>
                                        <li> .allowedOrigins("http://localhost:3000","http://localshot:3002);//  </li>
                                        <li> .allowedMethods("GET","POST","PUT","PATCH","DELETE","OPTIONS") //   </li>
                                        <li> .allowedHeaders("*") </li>
                                        <li> .allowCredentials(true) </li>
                                        <li> .allowedOriginPatterns("*") // .allowCredentials(true) + .allowedOrigins("*")  </li>
                                        <li> .maxAge(MAX_AGE_SECS); pre-flight Request    </li>
                                    </li>
                                </li>
                                <li> {'}'} </li>
                            </li>
                            <li> {'}'} </li>
                        </div>

                    </div>
                </details>
            </div>
        </div>
    );
}

export default SpringCors;